body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
  width: 100%;
  height: 60px;
  background-color: #39b54a;
  padding-left: 43px;
  padding-right: 43px;
  box-sizing: border-box;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.header__logo {
  color: #ffffff;
  font-family: Arial, sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 22px;
  text-decoration: none;
  display: inline;
  margin-right: auto;
}

.main {
  display: -webkit-flex;
  display: flex;
}

.page {
  max-width: 1369px;
  margin: 0 auto;
}

.sidebar {
  -webkit-flex-basis: 250px;
          flex-basis: 250px;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  height: 100%;
  border-right: 1px solid rgba(150, 150, 150, 0.2);
}

.nav {
  box-sizing: border-box;
  width: 100%;
  padding: 30px;
}

.nav__title {
  font-size: 18px;
  margin-bottom: 46px;
}

.nav__list {
  list-style: none;
  padding: 0;
}

.nav__list-item {
  margin-bottom: 12px;
}

.nav__list-button::first-letter {
  text-transform: uppercase;
}

.nav__list-button {
  text-decoration: none;
  color: #404844;
  font-size: 15px;
  border: none;
  background: none;
  cursor: pointer;
  position: relative;
  outline: none;
  text-align: left;
}

.nav__list-button--active {
  color: #45b759;
}

.nav__list-button:hover {
  color: #45b759;
}

.nav__list-button--active::before {
  content: '';
  width: 2px;
  height: 100%;
  background-color: #45b759;
  position: absolute;
  left: -30px;
}

.content__products {
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.content__title {
  margin-left: 30px;
  color: #404844;
  font-size: 36px;
}

.product {
  box-sizing: border-box;
  -webkit-flex-basis: 212px;
          flex-basis: 212px;
  width: 212px;
  height: 344px;
  position: relative;
  border: 1px solid rgba(150, 150, 150, 0.2);
  padding-top: 20px;
  padding-right: 15px;
  padding-left: 15px;
  padding-bottom: 20px;
}

.product_img-wrapper {
  height: 150px;
  width: 150px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 69px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.product__image {
  max-width: 100%;
  height: auto;
  margin: 0 auto;
}

.product__price {
  font-size: 18px;
  margin: 0;
  margin-bottom: 10px;
}

.product__curency {
  font-size: 12px;
}

.product__title {
  font-size: 14px;
  margin: 0;
}

.product__volume {
  font-size: 14px;
  color: #b4b4b4;
  position: absolute;
  right: 15px;
  bottom: 20px;
  margin: 0;
}

.add-to-cart {
  position: absolute;
  width: 100%;
  height: 58px;
  top: 50%;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5);
  margin-top: -15%;
  display: none;
  -webkit-align-items: center;
          align-items: center;
}
.add-to-cart__btn {
  height: 40px;
  background: #45b759;
  border-radius: 100px;
  border: none;
  width: 80%;
  margin: 0 auto;
  display: block;
  color: #ffffff;
  text-align: center;
  font-size: 16px;
  line-height: 40px;
}
.add-to-cart__icon {
  background-image: url(/static/media/icon.cbf11813.svg);
  background-repeat: no-repeat;
  background-size: cover;
  width: 16px;
  height: 14px;
  display: inline-block;
}

.product:hover .add-to-cart {
  display: -webkit-flex;
  display: flex;
}

.quantity-selector {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 15px;
  padding-right: 15px;
  /* display: none; */
}

.add-to-cart__btn,
.quantity-selector__minus,
.quantity-selector__plus {
  outline: none;
  border: none;
}

.quantity-selector__minus,
.quantity-selector__plus {
  width: 40px;
  height: 40px;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: cover;
}

.quantity-selector__minus {
  background-image: url(/static/media/minus-icon.1a4193bb.svg);
}

.quantity-selector__plus {
  background-image: url(/static/media/plus-icon.a582fcbd.svg);
}

.quantity-selector__display {
  height: 40px;
  width: 80px;
  border: 1px solid #efefef;
  border-radius: 20px;
  background-color: #ffffff;
  text-align: center;
  line-height: 40px;
}

.shopping-cart {
  margin-left: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  height: 40px;
  background-color: #67c674;
  border-radius: 20px;
  padding-left: 20px;
  padding-right: 20px;
  cursor: pointer;
}

.shopping-cart__icon {
  background-image: url(/static/media/icon.cbf11813.svg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 15px;
  height: 15px;
  position: relative;
}

.shopping-cart__total {
  margin: 0;
  color: #ffffff;
  font-size: 17px;
  margin-left: 20px;
}

.shopping-cart__amount {
  position: absolute;
  padding: 1px 4px;
  background-color: #ffcf1b;
  border: 1px solid #67c674;
  box-sizing: border-box;
  border-radius: 20px;
  font-size: 10px;
  text-align: center;
  line-height: 100%;
  position: absolute;
  left: 45%;
  top: -40%;
}

.float-cart {
  position: fixed;
  right: 0;
  top: 0;
  width: 675px;
  height: 100vh;
  background-color: rgb(255, 255, 255, 1);
  z-index: 200;
  cursor: auto;
  display: none;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

.float-cart-header {
  height: 48px;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.float-cart-header,
.float-cart-footer {
  width: 100%;
  background-color: #f6f6f6;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  box-sizing: border-box;
}

.float-cart-header__title {
  color: #404844;
  font-size: 18px;
  font-weight: 500;
  margin: 0;
  margin-right: auto;
}

.float-cart-header__close-btn {
  margin: 0;
  margin-left: auto;
  background: none;
  background-image: url(/static/media/close-icon.5d408dca.svg);
  width: 12px;
  height: 12px;
  display: block;
  border: none;
  outline: none;
  cursor: pointer;
}

.float-cart-content {
  overflow-y: scroll;
}

.float-cart-footer {
  height: 92px;
  margin-top: auto;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
}

.float-cart-footer__order-btn {
  background-color: #45b759;
  display: block;
  border: none;
  outline: none;
  cursor: pointer;
  width: 324px;
  height: 48px;
  font-size: 18px;
  color: #ffffff;
  border-radius: 34px;
  margin: 0;
  margin-right: auto;
}

.float-cart-footer__total-price {
  margin: 0;
  margin-left: auto;
  color: #404844;
  font-size: 18px;
}

.cart-product {
  height: 85px;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding-left: 30px;
  padding-right: 20px;
  box-sizing: border-box;
  border-bottom: 1px solid rgba(150, 150, 150, 0.2);
}

.cart-product__image {
  max-width: 50px;
  height: 50px;
  margin: 0;
  margin-right: 20px;
}

.cart-product__details {
  height: 100%;
  color: #404844;
  font-size: 12px;
}

.cart-product__title {
}

.cart-product__price {
}

.cart-product__curency {
}

.cart-product__volume {
  margin-left: 20px;
  display: inline-block;
  color: #b4b4b4;
  font-size: 14px;
}

.cart-product__total-price {
  /* margin-left: auto; */
  /* float: left; */
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  -webkit-flex-basis: 100px;
          flex-basis: 100px;
  text-align: right;
}

.quantity-selector--float-cart {
  width: 170px;
  margin-left: 20px;
  margin-right: 20px;
}

